.welcome-msg{
  height: 5vh;
  width: 11vw;
  align-items: center;
  justify-content: center;
  position: relative;
  margin: 0px 30px 10px 0px;
  display: inline-block;
  text-decoration: none;
  color:rgb(70, 135, 136);
}

.hover-style {
  height: 5vh;
  width: 11vw;
  color: rgb(70, 135, 136);
  cursor: pointer;
  align-items: center;
  justify-content:space-between;
  position: relative;
  margin-bottom: 5px;
  display: inline-block;
  text-decoration: none;
}

.noselect {
  position: relative;
  margin-bottom: 5px;
  display: inline-block;
  text-decoration: none;
}

.noselect::after {
  content: '';
  display: block;
  width: 0;
  height: 2px;
  background: rgb(70, 135, 136);
  transition: width .5s;
}

.noselect:hover::after {
  width: 100%;
}
